<template>
    <div class="Blog">
       <article @click="toDetails(url)">
            <h4 class="title">
                {{title}}
            </h4>
            <p class="time">
                {{time}}
                <el-tag v-for="(item, index) in tags"
                        :type="item.type"
                        size="mini"
                        :key="index">{{item.name}}</el-tag>
                {{distance(time)}}天前
            </p>
            <p v-html="returnBrief(brief)"
               class="brief">
                {{brief}}
            </p>
        </article>
    </div>
</template>
<script>
export default {
  name: 'Blog',
  props: {
    title: {type: String, default: () => ''},
    url: {type: String, default: () => ''},
    time: {type: String, default: () => ''},
    tags: {type: Array, default: () => []},
    brief: {type: String, default: () => ''}
  },
  methods: {
    // 跳转至详情
    toDetails (path) {
      this.$router.push('/blog/blog_' + path)
    },
    // 过滤标签后固定显示最多75字简介
    returnBrief (val) {
      const textNum = 80
      const _val = val.replace(/<[^>]+>/g, '')
      return _val.substring(0, textNum) + (_val.length <= textNum ? '' : '...')
    },
    // 计算距今的时间（天数）
    distance (time) {
      let now = new Date()
      let [year, month, date] = [time.substring(0, 4), time.substring(5, 7), time.substring(8, 10)]
      let before = new Date(year, month - 1, date)
      return ((now - before) / 1000 / 60 / 60 / 24) >> 0
    }
  }
}
</script>
<style lang="scss">
    @import './../assets/css/conifg/basic.config';

    .Blog {

        .el-tag{
            margin:0 2px;
        }

        article {

            & {
                cursor: pointer;
                padding: 0 4px;
                height: 100px;
                overflow: hidden;
                font-size: 12px;
                @include transition;
            }

            p {
                margin: 5px 0;
                font-size: 1em;
            }

            .title{
                border-left: 8px solid $primary-color;
                margin: 6px 0;
                padding-left: 4px;
                color: $dark-silver;
                font-size: 1.2em;
            }

            .time{
                padding-left: 10px;
                color: $light-silver;
                font-size: 0.9em;
            }

            .brief {
                padding: 0 10px;
                color: $dark-gray;
                font-size: 1em;
            }
        }
    }

    @media screen and ( min-width: 750px ) {
        .Blog article:hover {
            font-size: 14px;
        }
    }
</style>



